import React, { useEffect} from "react";
import Cookies from "js-cookie";
import { Redirect } from "react-router-dom";

const Logout = props => {
  useEffect(() => {
    Cookies.remove("admin_token");
  }, []);
  return (
    <div>
      <Redirect
        to={{
          pathname: "/admin/login",
          state: {
            from: props.location
          }
        }}
      />
    </div>
  );
};

export default Logout;
